 max-width: 350px;
 margin-top: 10px;
}

.container {
  border-radius: 23px;
  width: 380px; 
  height: 400px;
 
}
.titleTestimonios {
  font-size: 23px;
}

.subTitle {
text-align: center;
  font-size: 15px;
}

.container_testimonios{
  width: 380px;
  background-color: var(--color-black);
  border-bottom-left-radius: 26px;
  border-bottom-right-radius: 26px;

}

.testimonios {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background-color: rgba(0, 77, 64, 0.45); 
  border: 1px solid rgba(0, 77, 64, 0.3);
  box-shadow: 0 4px 10px rgba(255, 255, 255, 0.1);
  border-radius: 26px;
  margin: 20px 0;
  max-width: 370px;
  height: fit-content;
  flex-shrink: 0;
  gap: 5px;
 
}

.containerTestimoniosAll{
  height: 200px;
  overflow-x: scroll;
  overflow-y: hidden;
  display: flex;
  flex-direction: row;
  gap: 10px;
  flex-wrap: nowrap;
  max-width: 370px;
  
}

.textTestimonios p {
  
  font-size: 10px;
}

}